import React, { useState, useEffect } from "react";
import SearchForm from "./SearchForm";
import SearchResults from "./SearchResults";
import { getDataList, isInsideGeofence, text2Speech } from "./data.service";
import { Typography } from "@mui/material";

import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import LinearProgress from "@mui/material/LinearProgress";
import NotificationComponent from "./NotificationComponent";

interface NikooHomes {
  ec_extract: string;
  flatData: FlatDetailsData;
  owners: string;
}

interface FlatDetailsData {
  tower: string;
  flat: string;
}

const App: React.FC = () => {
  const [searchResults, setSearchResults] = useState<NikooHomes[]>([]);
  const [searchTerm, setSearchTerm] = useState<string>("");
  const [isMobile, setIsMobile] = useState(true);
  const [open, setOpen] = React.useState(false);
  const [showAlert, setShowAlert] = React.useState(false);
  let dataList = getDataList();

  const checkGeoFence = () => {
    if ("geolocation" in navigator) {
      console.log("inside if loop...geo");
      navigator.geolocation.getCurrentPosition(successCallback, errorCallback);
    } else {
      console.log("Geolocation is not supported by this browser.");
    }
  };

  const successCallback = (position: GeolocationPosition) => {
    const latitude = position.coords.latitude;
    const longitude = position.coords.longitude;
    if (isInsideGeofence(latitude, longitude)) {
      setShowAlert(true);
      console.log("Inside geofence.");
      if (searchTerm && searchTerm.trim().length > 0) {
        searchData(searchTerm);
      }
    } else {
      console.log("Outside geofence.");
      setOpen(true);
    }
  };

  const errorCallback = (error: GeolocationPositionError) => {
    console.error("Error accessing geolocation:", error.message);
  };

  const searchData = (searchTerm: string) => {
    setSearchResults([]);
    let results = dataList.filter((item) =>
      item.owners.toLowerCase().includes(searchTerm.toLowerCase())
    );
    if (!results) {
      results = dataList.filter((item) =>
        item.owners
          .toLowerCase()
          .includes(searchTerm.toLowerCase().replace(/\s+/g, ""))
      );
    }
    if (!results) {
      results = dataList.filter((item) =>
        item.flatData.flat.toLowerCase().includes(searchTerm.toLowerCase())
      );
    }
    if (results) {
      let resultText = "";
      if (results.length === 1) {
        resultText = `Tower ${results[0].flatData.tower}, Flat Number:
          ${results[0].flatData.flat}`;
      } else {
        resultText = `Multiple results found for ${searchTerm}`;
      }
      text2Speech(resultText);
      setSearchTerm(searchTerm);
      setSearchResults(results);
    }
  };

  useEffect(() => {
    setSearchResults([]);
    checkGeoFence();
    const userAgent = window.navigator.userAgent;
    const mobileKeywords = [
      "Mobile",
      "iPhone",
      "Android",
      "Windows Phone",
      "BlackBerry"
    ];
    const checkIsMobile = (): void => {
      const isMobileDevice = mobileKeywords.some((keyword) =>
        userAgent.includes(keyword)
      );
      setIsMobile(isMobileDevice);
    };
    checkIsMobile();
  }, []);

  return (
    <>
      <h1>NIKOO HOMES-II Owner's Search (Feb - 2023 Data)(v-1.0.4)</h1>
      {isMobile ? (
        <div>
          <SearchForm searchData={searchData} />
          {!searchResults && searchTerm && <LinearProgress />}
          <br />
          {searchResults && (
            <SearchResults results={searchResults} queryData={searchTerm} />
          )}
        </div>
      ) : (
        <Typography variant="h3" component="h2">
          This website is only accessible through a mobile device. Please visit
          using a mobile device to access the content.
        </Typography>
      )}
      <NotificationComponent
        show={showAlert}
        message="Inside NIKOO HOMES-II premises"
      ></NotificationComponent>
      <Dialog
        open={open}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          {"NIKOO HOMES-II Owners's Search"}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Accessing the restricted data outside the premises is not permitted.
          </DialogContentText>
        </DialogContent>
      </Dialog>
    </>
  );
};

export default App;
